import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import Main from '../components/section/Main';
import axios from 'axios';


const Live = () => {
    const [arrivalInfo, setArrivalInfo] = useState(null); // 도착 정보 상태

    const location = useLocation();
    const stationName = location.state && location.state.stationName;

    const fetchDataFromFlask = async (stationName) => {
        try {
            // stationName을 JSON 형식으로 직렬화하여 요청 본문에 포함
            const requestData = JSON.stringify({ stationName });

            // Flask 서버로 stationName을 전달하고 도착 정보를 받아옴
            const response = await axios.post(
                `http://flask:8082/subway_arrival`,
                requestData,
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                }
            );

            // 받아온 도착 정보를 상태에 저장
            setArrivalInfo(response.data);
        } catch (error) {
            console.error('데이터 가져오기 실패:', error);
        }
    };

    // 컴포넌트가 마운트될 때 데이터 가져오기
    useEffect(() => {
        fetchDataFromFlask();
    }, []);

    return (
        <Main
            title="상행하행"
            description="테스트 페이지"
        >
            {arrivalInfo ? (
                <div>
                    <h2>실시간 도착 정보: {stationName}</h2>
                    <ul>
                        {/* 선택한 키만 출력 */}
                        {Object.keys(arrivalInfo).filter(key => key === '원하는키').map((train, index) => (
                            <li key={index}>
                                <h3>{train}</h3>
                                <ul>
                                    {Object.keys(arrivalInfo[train]).map((key, idx) => (
                                        <li key={idx}>
                                            {key}: {arrivalInfo[train][key]}
                                        </li>
                                    ))}
                                </ul>
                            </li>
                        ))}
                    </ul>
                </div>
            ) : (
                <div>
                    <h1>실시간 도착정보가 없습니다.</h1>
                    <h4>데이터를 가져오는 중입니다...</h4>
                </div>
            )}
        </Main>
    );
};

export default Live;